body{
  height: 100vh;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.colorPallete{
  border-radius: 100%;
  border: 2px solid black;
  padding: 15px;
  margin: 0px 5px;
}

.yellow{
  background-color: rgb(250, 250, 45);
}
.green{
  background-color: rgb(173, 235, 137);
}
.blue{
  background-color: rgb(55, 212, 252);
}
